import ajax from '@/services/ajax';
import api from '@/services/api';

export function getAccountCancellationIndex(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getAccountCancellationIndex );
}

export function getAccountCancellationInfo(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getAccountCancellationInfo );
}

export function getAccountCancellationSuccess(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getAccountCancellationSuccess );
}



