import ajax from '@/services/ajax';
import api from '@/services/api';

export function getAccountOpeningIndex(data) {
    return ajax.postAfterLoginQuery( '960800', data, api.getAccountOpeningIndex );
}

export function getAccountOpeningPay(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getAccountOpeningPay );
}

export function getAccountOpeningSuccess(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getAccountOpeningSuccess );
}



