import ajax from '@/services/ajax';
import api from '@/services/api';

export function getAccountLiftLevelIndex(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getAccountLiftLevelIndex );
}

export function getAccountLiftLevelInfo1(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getAccountLiftLevelInfo1 );
}

export function getAccountLiftLevelInfo2(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getAccountLiftLevelInfo2 );
}

export function getAccountLiftLevelSuccess(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getAccountLiftLevelSuccess );
}



